.main-nav {
  height: 70px;
  position: fixed;
  width: 100%;
  background: #fff;
  opacity: 0;
  z-index: -1;
  transition-property: opacity;
  transition-duration: 0.5s;
  padding-top: 6px;
  padding-bottom: 8px;

  &.visible {
    z-index: 1011;
    opacity: 1;
    box-shadow: 0px 7px 30px rgb(0 0 0 / 15%);
    padding-right: 17px;
    transition-property: opacity;
    transition-duration: 0.5s;
  }

  .main-menu {
    width: 100%;
    margin-right: 35px;

    ul {
      padding: 0;
      margin: 0;
      margin-right: 51px;
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      li {
        position: relative;
        height: 50px;
        display: flex;
        align-items: center;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        letter-spacing: normal;
        line-height: 28px;
        position: relative;
        list-style: none;
        margin-bottom: 5px;

        &:last-child:before {
          display: none;
        }

        &:before {
          content: '';
          width: 6px;
          height: 6px;
          background-color: #fec400;
          margin-right: 10px;
          position: absolute;
          top: 22px;
          right: -53px;
          left: auto;
          border-radius: 100%;
        }
      }
    }
  }

  .main-btn {
    min-width: 250px;
    min-height: 56px;
    border: none;
    position: relative;
    font-size: 12px;
    line-height: 17px;
    z-index: 1;
    border-radius: 30px;
  }

  a {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: #13141c;
    text-decoration: underline;
    text-decoration-color: rgb(0 0 0 / 10%);
  }
}